}

.footer-buttons-text:hover {
    color: #4e46e5;
}

.lastpage-page {
    position: absolute;
    left: 0%;
    right: 0%;
    top: 990%;
    bottom: 0%;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
}

.footer-links-margin {
    margin-right: 10%;
}

.modal-window {
    position: fixed;
    z-index: 1;
    padding-top: 15%;
    left: 0;  
    top: 0;  
    width: 100%;  
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0, 0, 0); 
    background-color: rgba(0, 0, 0, 0.8);      
}

.modal-content {
    background-color: #fefefe; 
    margin: auto; 
    padding: 20px; 
    border: 1px solid #888; 
    width: 20%; 
    border-radius: 5px;   
}

.modal-header {
    margin-top: 0; 
    color: #aaaaaa; 
    font-size: 20px;
}

.model-content-text {
    font-family: 'Montserrat';
    font-style: normal; 
    font-size: 24px;
}

.modal-footer {
    text-align: right;
}

.close-modal-button {
    background-color: rgb(0, 120, 210);  
    font-family: 'Montserrat'; 
    border: none; 
    color: white; 
    padding: 10px; 
    text-align: center; 
    display: inline-block; 
    font-size: 16px;
    border-radius: 4px;
}
